import { configureStore } from "@reduxjs/toolkit";

import { reducer as itemsReducer } from "features/items/slice/reducer";
import { reducer as notifierReducer } from "features/notifier/slice/reducer";
import { reducer as tagsReducer } from "features/tags/slice/reducer";
import { reducer as periodsReducer } from "features/periods/slice/reducer";
import { reducer as viewReducer } from "features/view/slice/reducer";

const store = configureStore({
  reducer: {
    items: itemsReducer,
    tags: tagsReducer,
    periods: periodsReducer,
    notifier: notifierReducer,
    view: viewReducer
  },
});

export default store;
