import { useNavigate } from "react-router-dom";

import { Container } from "shared/ui/container";
import { Button } from "shared/ui/button";
import { ListIcon } from "shared/icons/list";
import { StatsIcon } from "shared/icons/stats";
import { routes } from "shared/constants/routes";
import { useStatsPage } from "shared/hooks/useStatsPage";

import s from "./style.module.css";

export const Footer = () => {
  const isStatsPage = useStatsPage();
  const navigate = useNavigate();

  const handleStats = () => {
    navigate(routes.stats);
  };

  const handleItems = () => {
    navigate(routes.root);
  };

  return (
    <>
      <div className={s.Offset}></div>
      <footer className={s.Footer}>
        <Container className={s.Container}>
          <div className={s.Action}>
            {isStatsPage ? (
              <Button ghost block onClick={handleItems}>
                <ListIcon /> Траты
              </Button>
            ) : (
              <Button ghost block onClick={handleStats}>
                <StatsIcon /> Статистика
              </Button>
            )}
          </div>
          <div className={s.Action}></div>
        </Container>
      </footer>
    </>
  );
};
