import { forwardRef, useImperativeHandle, useRef } from "react";

import { TModalRef } from "shared/types";
import { Button } from "shared/ui/button";
import { Panel } from "shared/ui/panel";
import { TNewPeriodRef } from "widgets/periods/types";
import { PeriodsList } from "widgets/periods/ui/periodsList";
import { NewPeriod } from "widgets/periods/ui/newPeriod";

import s from "./style.module.css";

export const PeriodsPanel = forwardRef<TModalRef>((_, ref) => {
  const newPeriodRef = useRef<TNewPeriodRef | null>(null);
  const periodsPanelRef = useRef<TModalRef | null>(null);

  const handleAddPeriod = () => {
    newPeriodRef.current?.addPeriod();
  };

  const handleSelectPeriod = () => {
    periodsPanelRef.current?.close()
  }

  useImperativeHandle(ref, () => {
    return {
      open: () => {
        periodsPanelRef.current?.open();
      },
      close: () => {
        periodsPanelRef.current?.close();
      },
      toggle: () => {
        periodsPanelRef.current?.toggle();
      },
    };
  });

  return (
    <>
      <Panel ref={periodsPanelRef}>
        <Button
          block
          size="l"
          className={s.PanelAction}
          onClick={handleAddPeriod}
        >
          Начать новый период
        </Button>

        <PeriodsList onSelect={handleSelectPeriod} />
      </Panel>

      <NewPeriod ref={newPeriodRef} />
    </>
  );
});
